export async function downloadImages(url) {
    const images = [];
    const requestsByCycle = 5;
    let cycle = 0;
    while (true) {
        const promises = [];
        for (let i = cycle * requestsByCycle; i < (cycle + 1) * requestsByCycle; i++) {
            const promise = loadImage(url + `${i}.png`).then((image) => {
                images[i] = image;
            });
            promises.push(promise);
        }
        const results = await Promise.allSettled(promises);
        if (results.some((res) => res.status == 'rejected')) {
            break;
        }
        cycle++;
    }
    if (images.length == 0) {
        throw new Error(`Not image found at url: "${url}"`);
    }
    return images;
}
export function loadImage(src) {
    return new Promise(function (resolve, reject) {
        const image = Object.assign(document.createElement('img'));
        function cleanup() {
            image.onload = null;
            image.onerror = null;
        }
        image.onload = function () {
            cleanup();
            resolve(image);
        };
        image.onerror = function () {
            cleanup();
            reject(new Error('Failed to load the image "' + src + '"'));
        };
        image.src = src;
        image.crossOrigin = 'anonymous';
    });
}
